export default function getArticle() {
    fetch("/getarticle", {
        method: 'GET'
    }).then(response => {
        const data = response.text()
        return data


    }).then(text => {
        const jsondata = JSON.parse(text)
        const list = document.querySelector('.entries__list')
        const title = document.querySelectorAll('.entry__title')
        console.log(jsondata)
        const articles = jsondata.articles

        for (let i = 0; i < articles.length; i++) {
            console.log(articles[i])
            for (let j=0; j<title.length; j++) {
                if (title[j] === articles[i].title) {
                    console.log(111)
                    return
                }
            }
            const li = document.createElement('li')
            li.classList.add('entries__item')
            const content = `
            <div class="entry__content">
                <h2 class="entry__title">${articles[i].title}</h2>
                <div class="entry__date">${articles[i].date.substring(0, 10)}</div>
                <div class="entry__text">${articles[i].text}</div>
            </div>
            `
            li.innerHTML = content
            list.appendChild(li)
        } 
    })
}

